
<template>
  <AppHeader />
  <div>
    <router-view
      :cart="cart"
      :add-to-cart="addToCart"
      :remove-from-cart="removeFromCart"
    />
  </div>
  <AppFooter />
</template>

<script setup>
import { ref } from "vue";
import AppHeader from "@/components/AppHeader.vue";
import AppFooter from "@/components/AppFooter.vue";

// ì¥ë°”êµ¬ë‹ˆ ìƒíƒœ
const cart = ref([]);

// ì¥ë°”êµ¬ë‹ˆì— ìƒí’ˆ ì¶”ê°€ í•¨ìˆ˜
const addToCart = (product) => {
  const itemIndex = cart.value.findIndex((item) => item.id === product.id);
  if (itemIndex === -1) {
    cart.value.push({ ...product, quantity: product.quantity });
  } else {
    cart.value[itemIndex].quantity += product.quantity;
  }
};

// ì¥ë°”êµ¬ë‹ˆì—ì„œ ìƒí’ˆ ì‚­ì œ í•¨ìˆ˜
const removeFromCart = (productId) => {
  cart.value = cart.value.filter((item) => item.id !== productId);
};
</script>
